.sidebar {
  width: 18%;
  background-attachment: scroll;
  background-position: 0 0;
  position: fixed;
  top: 0;
  bottom: 0;
  text-align: right;
  header {
    margin-top: 80%;
    padding: 0 $scale;
  }
}

.content-main {
  padding: $scale3 $scale2 $scale3 $scale-1;
  margin-left: 20%;
}

footer {
  margin-left: 20%;
  max-width: 46em;
}
.previous-next {

  &.archive {
    text-align: center;
  }
  margin-bottom: $scale1;
}

