
@import 'base16/base16-paraiso.scss';


//大部份的class可以直接使用 markdown的```块语法，然后添加对应的 .class_name 来实现

//配方 formula name
.formula{
    background-color: $base16-color-0b;
    margin: 0px;
}

.herb {
    background-color: $base16-color-0b;
    display: block;
    width: 50%;
    padding: 20px;
}




.post .content a {
    text-decoration-line: underline !important;
}


//引用
.quote {
 font-weight: bold ;
}


//重点
.emphasis {
    font-weight: bolder;
}

.comment {

    margin-left: 30px;
    display: block;
    width: 80%;
    white-space: normal;
    border: 1px dashed rgb(55, 138, 52);
    border-left: 25px solid  rgb(77, 160, 74);
    
    border-radius: 2px;
    padding: 15px 25px 15px;
}